"use client";
import { useEffect, useState } from "react";
import { v4 as uuidv4 } from "uuid";

export default function ChatPage() {
  const [user, setUser] = useState(null);
  const [sessionId] = useState(() => uuidv4());
  const [chatHistory, setChatHistory] = useState([]);
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);

  // Get user info from localStorage
  useEffect(() => {
    const storedUser = localStorage.getItem("user");
    if (storedUser) {
      setUser(JSON.parse(storedUser));
    } else {
      window.location.href = "/login"; // redirect if not logged in
    }
  }, []);

  // Fetch chat history after user is set
  useEffect(() => {
    if (!user) return;

    fetch(`http://localhost:8000/get_chats/${user.user_id}`)
      .then((res) => res.json())
      .then((data) => {
        // Optional: sort by timestamp
        const sorted = data.sort(
          (a, b) => new Date(a.timestamp) - new Date(b.timestamp)
        );
        setChatHistory(sorted);
      })
      .catch((err) => console.error("Failed to fetch chat history", err));
  }, [user]);

  const handleSend = async () => {
    if (!message.trim()) return;
    setLoading(true);

    // Simulate bot response (replace with LangChain API call if needed)
    const botResponse = `Echo: ${message}`;

    const chatPayload = {
      user_id: user.user_id,
      session_id: sessionId,
      user_message: message,
      bot_response: botResponse,
    };

    try {
      const res = await fetch("http://localhost:8000/save_chat/", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(chatPayload),
      });

      if (!res.ok) throw new Error("Failed to save message");

      // Add new message to chat UI immediately
      setChatHistory((prev) => [...prev, { ...chatPayload }]);
      setMessage("");
    } catch (err) {
      console.error(err);
      alert("Failed to send message");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-black text-white flex flex-col items-center p-6">
      <h1 className="text-2xl font-bold mb-4">Chat with Bot</h1>

      <div className="w-full max-w-2xl bg-gray-900 rounded-xl p-4 overflow-y-auto h-[400px] mb-4 shadow-lg">
        {chatHistory.map((chat, index) => (
          <div key={index} className="mb-4">
            <div className="text-purple-400 font-semibold">You:</div>
            <div className="mb-2">{chat.user_message}</div>
            <div className="text-green-400 font-semibold">Bot:</div>
            <div>{chat.bot_response}</div>
            <hr className="my-2 border-gray-700" />
          </div>
        ))}
      </div>

      <div className="w-full max-w-2xl flex space-x-2">
        <input
          type="text"
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          className="flex-1 px-4 py-2 rounded-lg bg-black border border-purple-500 text-white focus:outline-none focus:ring-2 focus:ring-purple-400"
          placeholder="Type your message..."
        />
        <button
          onClick={handleSend}
          disabled={loading}
          className="px-4 py-2 bg-purple-600 hover:bg-purple-700 rounded-lg disabled:opacity-50"
        >
          {loading ? "Sending..." : "Send"}
        </button>
      </div>
    </div>
  );
}
